@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,700&display=swap);
/* https://pixabay.com/pl/photos/tabela-jedzenie-p%c5%82%c3%b3tno-z-drewna-3219660/ */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 22px;
    font-family: 'Noto Sans', sans-serif;
    color: rgb(77, 14, 14);
}

#root {
    position: relative;
    height: 100vh;
    width: 100vw;
    background-image: url(/Waitress_Assistant/static/media/background.0ec39376.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.navigation nav {
    width: 100%;
}

.navigation ul {
    display: flex;
    justify-content: space-around;
    list-style: none;
}

.navigation ul li a {
    text-transform: uppercase;
    text-decoration: none;
    color: rgb(77, 14, 14);
}

.navigation ul li a:hover {
    text-decoration: underline;
    color: rgb(240, 232, 232)
}
.table {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 15%;
    border-radius: 25px;
    margin: 10px;
    border: 2px solid black;
}

.free {
    background-color: rgba(112, 155, 12, 0.7);
}

.taken {
    background-color: rgba(238, 17, 17, 0.7);
}

.tables .table h1 {
    margin: 5px 0;
    font-size: 1rem;
    text-align: center;
    color: white;
}

.table div.picture {
    width: 100%;
    height: 80px;
    background-image: url(/Waitress_Assistant/static/media/table.e8eb29bf.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.tables .table a {
    font-size: 0;
    margin: 5px 0;
    width: 80%;
}

.panel .table a button {
    width: 100%;
    height: 20px;
}

@media(max-width: 1024px) {
    .table div.picture {
        width: 80%;
        height: 75px;
    }
}

@media(orientation: portrait) and (min-width: 740px) {
    .table {
        flex-basis: 20%;
    }
}
.tables {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.orderForm form {
    display: flex;
}

.orderForm form label {
    display: flex;
}

.orderForm form label select {
    margin: 0 20px 0 5px;
}

.orderForm form label input {
    width: 50px;
    margin: 0 20px 0 5px;
    text-align: center;
}

.orderForm div:nth-of-type(1) {
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
}
.orderList {
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
}
.listItems {
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
    list-style: none;
}

.listItems li {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
}

.listItems li span:first-child {
    flex-grow: 1;
}

.listItems li span:nth-child(2) {
    margin-right: 12px;
    font-weight: bold;
}
.addEditForm form label {
    display: block;
    position: relative;
    width: 60%;
    margin-bottom: 10px;
}

.addEditForm form label input {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 70%;
    padding: 5px
}

.addEditForm form label:nth-child(2) input {
    width: 20%;
    text-align: center;
}

.errors {
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(110%, 0);
            transform: translate(110%, 0);
    height: 100%;
}
.selection {
    position: relative;
    width: 100%;
}

.selection p {
    display: inline-block;

}

.panel .selection button {
    width: 65px;
    margin: 0;
}

.panel .selection button:nth-of-type(1) {
    position: absolute;
    top: 0;
    left: 65%;

}

.panel .selection button:nth-of-type(2) {
    position: absolute;
    top: 0;
    left: 80%;
}

.summary {
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
}
.app {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 5%;
    bottom: 5%;
    left: 20%;
    right: 2%;
    background-color: rgba(201, 193, 193, 0.5);
    border-radius: 20px;
}

header,
footer {
    border-left: 150px solid transparent;
    border-right: 150px solid transparent;
}

header {
    flex-basis: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 4px solid rgb(77, 14, 14);
    font-size: 2rem;
    font-style: italic;
    text-transform: uppercase;
    color: rgb(77, 14, 14);
}

main {
    flex-basis: 90%;
    display: flex;
    flex-direction: column;
}

main .navigation {
    flex-basis: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 150px solid transparent;
    border-right: 150px solid transparent;
    border-bottom: 4px solid rgb(77, 14, 14);
}

.panel {
    flex-grow: 1;
    margin: 20px;
}

.panel a {
    font-size: 0;
}

.panel button {
    margin-right: 10px;
    height: 30px;
    width: 50px;
}

.panel h1 {
    font-size: 1.2rem;
    margin-bottom: 10px;
}

.panel table {
    width: 100%;
    margin-bottom: 10px;
    font-size: 0.8rem;
    background-color: rgba(252, 247, 247, 0.5);
}

.panel table,
th,
td {
    border: 2px solid rgb(77, 14, 14);
    border-collapse: collapse;
    padding: 3px;
}

.panel table tr:nth-child(1) th:nth-child(2) {
    width: 15%;
}

.panel table tr:nth-child(1) th:nth-child(3) {
    width: 20%;
}

.panel table td:nth-child(1) {
    text-align: left;
    padding-left: 10px;
}

.panel table td:nth-child(2),
.panel table td:nth-child(3) {
    text-align: center;

}

.panel table td:nth-child(3) button {
    margin-right: 0;
    width: 80%;
}


footer {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7rem;
    border-top: 4px solid rgb(77, 14, 14);
    color: rgb(77, 14, 14);
}

@media(orientation: portrait) and (min-width: 740px) {
    header {
        flex-basis: 7%;
        font-size: 1.5rem;
    }

    header,
    footer {
        border-left: 80px solid transparent;
        border-right: 80px solid transparent;
    }

    main .navigation {
        border-left: 80px solid transparent;
        border-right: 80px solid transparent;
    }
}
